<template>
  <div>
    <myHeader></myHeader>
    <div style="margin:0 auto;width:1100px; ">
    <div style="margin:0 auto;width:800px; float:left;">
      <!--  -->
      <div style="border-bottom: 1px solid #cccccc;">
        <div>
            <h2>
                {{news.title}}
            </h2>
        </div>
        <div style="margin:5px">
            <span class="other-left">
                <span>{{news.publishTime}}</span>
                  {{news.publisher}}
            </span>
        </div>
      </div>

      <!--  -->
      <div>
        <center>
          <img :src="news.newsPic" :alt="news.title" style="margin:5 auto;max-width: 660px;">
        </center>
      </div>

      <!--  -->
      <div class="artical-main-content" v-html="news.context">
      </div>
    </div>
    <div>
      <hotSpotNews></hotSpotNews>
    </div>

    </div>
  </div>
</template>
<script>
import axios from 'axios'
import myHeader from './../public/Header'
import hotSpotNews from './HotSpotNews'
import config from './../../router/config'
export default{
  components:{
    myHeader,
    hotSpotNews,
  },
  data(){
    return{
      rootURL: config.rootURL,
      publishId:'',
      news:{
        publishId:'',
        publishTime:'2017-10-31 19:17:00',
        publisher:'',
        title:''
      },
    }
  },
  methods:{
    timeago(time){   //dateTimeStamp100013
            var minute=1000*60;      //
            var  hour=minute*60;
            var day=hour*24;
            var week=day*7;
            var halfamonth=day*15;
            var month=day*30;

            var  now=new Date().getTime();   //
            var dateTimeStamp = Date.parse(new Date(time));

            console.log('timeago'+now);
            console.log('timeago'+dateTimeStamp);
            var diffValue=now-dateTimeStamp;//

            if(diffValue<0){return;}

            var  minC=diffValue/minute;  //
            var  hourC=diffValue/hour;
            var  dayC=diffValue/day;
           var  weekC=diffValue/week;
            var  monthC=diffValue/month;
            var result;
            console.log(minC);
           if(monthC>=1){
             result=" "+parseInt(monthC)+""
              }

         else  if(weekC>=1){
             result=" "+parseInt(weekC)+""
              }
        else    if(dayC>=1){
             result=" "+parseInt(dayC)+""
              }
       else    if(hourC>=1){
             result=" "+parseInt(hourC)+""
              }
       else    if(minC>=1){
             result=" "+parseInt(minC)+""
              }
        else{
        result="";
        }
let that=this;
that.time=result;
     return result;
   },
   getNewsById(){
     let that = this ;

     axios.get(that.rootURL+'/getNewsById.do?newsId='+that.publishId).then(function(res){
         that.news={};
         that.news.publishId=res.data.newsId;
         that.news.publishTime=res.data.newsPublishTime;
         that.news.publisher=res.data.newsPublisher;
         that.news.title=res.data.newsTitle;
         that.news.newsPic=res.data.newsPic;
         that.news.context=res.data.newsContext;
     });
   }
 },
 created() {
   let that = this ;
   that.publishId= that.$route.params.publishId;
   console.log(that.$route.params.publishId);
   that.getNewsById();
   //that.hsytt();
 },
 // watch:{
 //   '$route':'hsytt'
 // }
}
</script>

<style scoped>
.news{
  float:left;
  width:740px;
  height: 90px;
  margin: 5px;
  /*border-style: inherit;*/
}
.other-left{
  color: gray;
}
.artical-main-content{
  color: #1a2939;
line-height: 24px;
font-size: 14px;
}

</style>



